angular.module('angular-meteor').run(['$templateCache', function($templateCache) {$templateCache.put("client/views/parties/parties-list.ng.html", "<form> <label>Name</label> <input ng-model=\"newParty.name\"> <label>Description</label> <input ng-model=\"newParty.description\"> <label>Public</label> <input type=\"checkbox\" ng-model=\"newParty.public\"> <button ng-click=\"newParty.owner=$root.currentUser._id; parties.push(newParty)\">Add</button> <button ng-click=\"removeAll()\">Remove All</button> </form> <ul> <li ng-repeat=\"party in parties\"> <a href=\"/parties/{{party._id}}\">{{party.name}}</a> <p>{{party.description}}</p> <button ng-click=\"remove(party)\">X</button> </li> </ul>");}]);